body{
    width: 100%;
    box-sizing: border-box;
    font-family: 'Catamaran', sans-serif;
    margin: 0;
    overflow: hidden;
    background-color: black;
}

.forest-container{
    width: 100%;
    padding-bottom: 100vh;
    padding-top: 10%;
     background-image: url(../../src/img/dimmed-forest-desktop.png);
     background-size: cover;
    background-position-x: 20%;
    background-position-y: 0px;
    background-repeat: no-repeat;

    .avatar-logo{
        width: 70%;
        position: relative;
        padding-bottom: 20%;
        margin: 0 auto;
        background-image: url(../../src/img/avatar-logo.png);
        background-size: contain;
        background-repeat: no-repeat;
        margin: 0 auto;
        margin-bottom: 5%;
        margin-top: 5%;
        z-index: 10000;
    }
    
    .plant-glow-container{
            width: 100%;
            height: 100vh;
            position: absolute;

        .plant-glow{
            width: 100%;
            position: absolute;
            top: 22vh;
            left: -17px;
            opacity: 0;
            transition: all .8s;
            background-image: url(../../src/img/plant-glow.png);
            background-size: cover;
            padding-bottom: 100%;
            background-repeat: no-repeat;
            z-index: 9;

            img{
                width: 100%;
            }
        }

        .small-pink-glow{
            position: absolute;
            width: 45%;
            left: 60%;
            top: 14%;
            opacity: 0;

            img{
                width: 100%;
            }
        }

        

    }
    .large-pink-glow{
            width: 61%;
            max-width: 300px;
            position: fixed;
            left: 13%;
            top: 4%;
            opacity: 0;
            

            img{
                width: 100%;
            }
        }

    .dinosaur-container{
        width: 100%;
        position: absolute;
        z-index: 100;

        .dinosaur{
            width: 94%;
            position: absolute;
            max-width: 250px;
            right: -38px;
            top: 54vh;
            background-image: url(../../src/img/full-dino.png);
            background-repeat: no-repeat;
            padding-bottom: 50%;
            background-size: contain;
            transform: scale(1.5);

        }
    }
}



@media screen and  (min-width: 768px){
    .forest-container{
        background-image: url(../../src/img/dimmed-forest-desktop.png);
        padding-bottom: 80%;

        .avatar-logo{
            margin-top: 0;
            margin-bottom: 0;
            max-width: 550px;
        }

        .dinosaur-container{
                top: 0;
                right: 98px;
        }

    }
}

@media screen and  (min-width: 1024px){
    .forest-container{
        background-image: url(../../src/img/dimmed-forest-desktop.png);
        padding-bottom: 60%;

        .dinosaur-container{
            top: 210px;
            right: 98px;
        }
    }
}

@media screen and  (min-width: 1366px){
    .forest-container{
        background-image: url(../../src/img/dimmed-forest-desktop.png);
        padding-bottom: 40%;
        background-position: center;
        
    }
}
